.mentor {
    display: flex;
    width: 50%;
}

.mentor-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
}

.mentor-info > * {
    margin: 10px;
}

.mentor-img {
    height: 160px;
    margin: 15px;
    border-radius: 100%;
}